<template>
    <div class="calculator">
        <div class="display">{{ currentNumber || 0 }}</div>
        <button @click="clear">C</button>
        <button @click="sign">+/-</button>
        <button @click="percent">%</button>
        <button @click="setOperator('divide')" class="operator">รท</button>
        <button @click="addNumber('7')">7</button>
        <button @click="addNumber('8')">8</button>
        <button @click="addNumber('9')">9</button>
        <button @click="setOperator('multiply')" class="operator">x</button>
        <button @click="addNumber('4')">4</button>
        <button @click="addNumber('5')">5</button>
        <button @click="addNumber('6')">6</button>
        <button @click="setOperator('subtract')" class="operator">-</button>
        <button @click="addNumber('1')">1</button>
        <button @click="addNumber('2')">2</button>
        <button @click="addNumber('3')">3</button>
        <button @click="setOperator('add')" class="operator">+</button>
        <button @click="addNumber('0')" class="zero">0</button>
        <button @click="dot">.</button>
        <button @click="getResult" class="operator">=</button>
    </div>
</template>

<script>
export default {
    data() {
        return {
            previousNumber: null,
            operator: null,
            currentNumber: ''
        }
    },
    methods: {
        addNumber(number) {
            this.currentNumber += number;
        },
        clear() {
            this.currentNumber = '',
            this.operator = null,
            this.previousNumber = null
        },
        sign() {
            this.currentNumber = this.currentNumber.charAt(0) === '-' ? this.currentNumber.slice(1) : `-${this.currentNumber}`
        },
        percent() {
            this.currentNumber = parseFloat(this.currentNumber)/100
        },
        dot() {
            if(this.currentNumber.indexOf('.') === -1) {
                this.currentNumber += '.'
            }
        },
        setOperator(currentOperation) {
            this.operator = currentOperation
            this.previousNumber = this.currentNumber
            this.currentNumber = ''
        },
        getResult() {
            switch(this.operator) {
                case 'multiply':
                this.currentNumber = parseFloat(this.previousNumber) * parseFloat(this.currentNumber)
                break;

                case 'divide':
                this.currentNumber = parseFloat(this.previousNumber) / parseFloat(this.currentNumber)
                break;

                case 'subtract':
                this.currentNumber = parseFloat(this.previousNumber) - parseFloat(this.currentNumber)
                break;

                case 'add':
                this.currentNumber = parseFloat(this.previousNumber) + parseFloat(this.currentNumber)
                break;

            }
            this.previousNumber = null,
            this.operator = null
        }
        
    }
  
}
</script>

<style scoped>
    .calculator {
        margin: 0 auto;
        width: 400px;
        display: grid;
        font-size: 40px;
        grid-template-columns: repeat(4, 1fr);
        grid-auto-rows: minmax(50px, auto);
    }

    .display {
        grid-column: 1/5;
        background-color: #333;
        color: white;
    }

    .zero {
        grid-column: 1/3;
    }

    .operator {
        background-color: orange;
        color: white;
    }
</style>
